"use client";

import { useState } from 'react';
import { Flashcard as FlashcardType } from '@/lib/flashcards';
import { Card } from '@/components/ui/card';

interface FlashcardProps {
  card: FlashcardType;
}

export function Flashcard({ card }: FlashcardProps) {
  const [isFlipped, setIsFlipped] = useState(false);

  const handleFlip = () => {
    setIsFlipped(!isFlipped);
  };

  return (
    <div 
      className="flashcard"
      onClick={handleFlip}
      role="button"
      tabIndex={0}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          handleFlip();
        }
      }}
      aria-label={`Flashcard for ${card.word}`}
    >
      <div className={`flashcard-inner transform-style-3d ${isFlipped ? 'rotate-y-180' : ''}`}>
        {/* Front of card */}
        <Card className="flashcard-front backface-hidden">
          <h2 className="text-3xl font-bold text-gray-900 mb-4">{card.word}</h2>
          <p className="text-gray-600 text-sm">Click to see IELTS Band 8.5 synonyms</p>
        </Card>

        {/* Back of card */}
        <Card className="flashcard-back backface-hidden rotate-y-180">
          <div className="text-center">
            <h3 className="text-xl font-semibold text-gray-900 mb-4">Band 8.5 Synonyms</h3>
            <div className="space-y-2">
              {card.synonyms.map((synonym, index) => (
                <p key={index} className="text-gray-700">{synonym}</p>
              ))}
            </div>
            {card.example && (
              <div className="mt-4 pt-4 border-t border-gray-200">
                <p className="text-sm text-gray-600 italic">{card.example}</p>
              </div>
            )}
          </div>
        </Card>
      </div>
    </div>
  );
}
